"use client";
import funds from "@/data/funds.json";
import Link from "next/link";
import Image from "next/image";

function normalize(str: string) {
  return str.toLowerCase();
}

function categories() {
  return ["All", "Education", "Medical Aid", "War Widows", "Veterans"];
}

function FundCard({ fund }: { fund: (typeof funds)[number] }) {
  return (
    <div className="rounded-lg border p-4 bg-white shadow-sm">
      <div className="flex items-center gap-3">
        {fund.logoPath ? (
          <Image src={fund.logoPath} alt="" width={40} height={40} className="h-10 w-10 object-contain" />
        ) : null}
        <div>
          <h3 className="text-lg font-semibold">{fund.name}</h3>
          <p className="text-sm text-gray-600">{fund.shortDescription}</p>
        </div>
      </div>
      <div className="mt-3 flex flex-wrap items-center gap-2">
        <span className="text-xs rounded bg-gray-100 px-2 py-1 text-gray-700">
          {fund.category}
        </span>
        {fund.tags?.slice(0, 3).map((t) => (
          <span key={t} className="text-xs rounded bg-gray-50 px-2 py-1 text-gray-500">
            #{t}
          </span>
        ))}
      </div>
      <div className="mt-4 flex gap-3">
        <Link className="rounded bg-black px-3 py-2 text-white text-sm" href={`/funds/${fund.slug}`}>
          View Fund Details
        </Link>
        {fund.officialUrl ? (
          <a
            className="rounded border px-3 py-2 text-sm"
            href={fund.officialUrl}
            target="_blank"
            rel="noopener noreferrer"
          >
            Official Site
          </a>
        ) : null}
      </div>
    </div>
  );
}

function FundsDirectoryClient({ initial }: { initial: typeof funds }) {
  const params = typeof window !== "undefined" ? new URLSearchParams(window.location.search) : null;
  const initCat = params?.get("category") ?? "All";
  const [q, setQ] = React.useState("");
  const [cat, setCat] = React.useState(initCat);
  const filtered = initial.filter((f) => {
    const matchCat = cat === "All" || f.category === cat;
    const query = normalize(q);
    const matchQ =
      !query ||
      normalize(f.name).includes(query) ||
      normalize(f.shortDescription).includes(query) ||
      (f.tags || []).some((t) => normalize(t).includes(query));
    return matchCat && matchQ;
  });
  return (
    <div className="space-y-6">
      <div className="flex flex-col gap-3 md:flex-row md:items-center md:justify-between">
        <input
          value={q}
          onChange={(e) => setQ(e.target.value)}
          placeholder="Search funds, tags..."
          className="w-full md:w-1/2 rounded border px-3 py-2"
        />
        <select
          value={cat}
          onChange={(e) => setCat(e.target.value)}
          className="w-full md:w-56 rounded border px-3 py-2"
        >
          {categories().map((c) => (
            <option key={c} value={c}>
              {c}
            </option>
          ))}
        </select>
      </div>
      <div className="grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-3">
        {filtered.map((f) => (
          <FundCard key={f.id} fund={f} />
        ))}
      </div>
    </div>
  );
}

export default function Page() {
  return (
    <main className="mx-auto max-w-6xl px-4 py-10">
      <h1 className="text-3xl font-bold">Fund Directory</h1>
      <p className="mt-2 text-gray-600">
        Search and filter verified funds that support Indiaâ€™s armed forces and their families.
      </p>
      <div className="mt-8">
        <FundsDirectory initial={funds} />
      </div>
    </main>
  );
}

function FundsDirectory({ initial }: { initial: typeof funds }) {
  return <FundsDirectoryClient initial={initial} />;
}

// minimal React import for client component usage
import * as React from "react";
